<template>
  <v-tabs
    dark
    grow
    color="primary"
    show-arrows
    centered
  >
    <v-tabs-slider color="red"/>

    <v-tab
      href="#tab-1"
      key="1"
    >
      Details
    </v-tab>

    <v-tab
      href="#tab-2"
      key="2"
    >
      Ingredients
    </v-tab>

    <v-tabs-items>
      <v-tab-item
        value="tab-1"
        key="1"
      >
        <basic-info/>
      </v-tab-item>
      <v-tab-item
        value="tab-2"
        key="2"
      >
        <list
          :items="items"
        />
      </v-tab-item>
    </v-tabs-items>
  </v-tabs>
</template>

<script>
import BasicInfo from "@/components/ingredient/sidepanel/BasicInfo.vue"
import List from "@/components/dashboard/List.vue"

export default {
  components: {
    BasicInfo,
    List
  },
  data() {
    return {
      items: null
    }
  },
  created() {
    this.getSources()
  },
  methods: {
    getSources() {
      this.loading = true
      this.$Parse.Cloud.run("get_sources", {}).then(response => {
        if (response.status == "success") {
          this.items = this.sourceSetup(response.data)
          console.log(this.items)
        } else {
          console.log("Errored response")
        }
        this.loading = false
      }, error => {
        this.loading = false
        console.error(error)
      }); 
    },
    sourceSetup(response) {
      let typing = (x, type) => {
        return {
          ...x,
          className: type
        };
      }
      return {
        ingredients: response.ingredients.map(x => typing(x.toJSON(), "ingredient")),
        recipes: response.recipes.map(x => typing(x.toJSON(), "recipe"))
      }
    }
  }
}

</script>