<template>
  <v-list two-line class="list">
    <template
      v-for="(item, index) in processedItems"
      class="tile"
    >
      <v-list-tile
        ripple
        @click="action(item)"
        :key="item.objectId"
      >
        <v-list-tile-content>
          <v-list-tile-title v-text="item.name"></v-list-tile-title>
          <v-list-tile-sub-title class="text--primary">Created on {{ item.createdAt }}</v-list-tile-sub-title>
          <v-list-tile-sub-title>{{ item.description }}</v-list-tile-sub-title>
        </v-list-tile-content>

        <v-list-tile-action>
          <v-list-tile-action-text>{{ item.className }}</v-list-tile-action-text>
          <v-icon v-if="item.className == 'recipe'">restaurant</v-icon>
          <v-icon v-if="item.className == 'ingredient'">pie_chart</v-icon>
        </v-list-tile-action>
      </v-list-tile>
      <v-divider
        :key="item.index"
        v-if="index + 1 < items.length"
      />
    </template>
  </v-list>
</template>

<script>
export default {
  props: [
    "search",
    "items",
    "filters",
    "action"
  ],
  computed: {
    processedItems() {
      // return a user's items after filtering and searching through them.
      if (!this.items) {
        return []
      }
      let items = []
      if (!(this.filters && this.filters.ingredients.status)) {
        items = this.items.ingredients//.map(x => x.type = "ingredient")
      }
      if (!(this.filters && this.filters.recipes.status)) {
        items = items.concat(this.items.recipes)//.map(x => x.type = "recipe"))
      }
      let search = ""
      if (this.search) {
        search = this.search
      }
      return items.filter(item => 
        item.name.toLowerCase().includes(search.toLowerCase())
        || item.description.toLowerCase() || item.includes(search.toLowerCase()).sort(x => x.createdAt))
    }
  }
}

</script>

<style scoped>
.list {
  flex-grow: 1;
  overflow-y: auto;
}
.tile {
  width:100%
}
</style>