<template>
  <v-card flat>
    <v-card-text class="pb-1">
      <v-text-field
        name="name"
        v-model="set_name"
        color="primary"
        label="Name"
        required
      />
      <v-textarea
        name="description"
        v-model="set_description"
        color="primary"
        label="Description"
        required
        rows="3"
      />
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: [
    "name",
    "description"
  ],
  computed: {
    set_name: {
      get() {
        return this.name
      },
      set(val) {
        this.$emit("name", val)
      }
    },
    set_description: {
      get() {
        return this.description
      },
      set(val) {
        this.$emit("description", val)
      }
    }
  }
}
</script>
