<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :loading="loading"
    hide-actions
  >
    <template slot="items" slot-scope="props">
      <td>{{ props.item.name }}</td>
      <td>{{ props.item.amount }}</td>
      <td>{{ props.item.unit }}</td>
    </template>
  </v-data-table>
</template>

<script>
  export default {
    props: [
      "loading",
      "items"
    ],
    data () {
      return {
        headers: [
          {
            text: 'Property',
            align: 'left',
            value: 'name'
          },
          { text: 'Amount', value: 'amount' },
          { text: 'Unit', value: 'unit' },
        ]
      }
    }
  }
</script>