<template>
  <v-layout row class="recipe">
    <detail-tabs
      class="leftTabs"
    />
    <nutrition-table
      :loading="loading"
      :items="[]"
      class="table"
    />
    <nutrition-tabs
      class="rightTabs"
    />
    <v-btn
      color="primary"
      bottom right
      fixed
      dark
      fab
      @click="save()"
      slot="activator"
    >
      <v-icon>save</v-icon>
    </v-btn>
  </v-layout>
</template>

<script>
import DetailTabs from "@/components/recipe/DetailTabs.vue"
import NutritionTabs from "@/components/recipe/NutritionTabs.vue"
import NutritionTable from "@/components/recipe/NutritionTable.vue"

export default {
  components: {
    DetailTabs,
    NutritionTabs,
    NutritionTable
  },
  created() {
    let save = this.saveRecipe
    document.addEventListener("keydown", e => {
      if (e.keyCode == 83 && (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)) {
        e.preventDefault();
        save()
      }
    }, false);
    this.getRecipe()
  },
  methods: {
    getRecipe() {
      this.loading = true
      this.$Parse.Cloud.run("get_recipe", {object_id: this.$route.params.id}).then(response => {
        console.log(response)
        this.loading = false
      }, error => {
        console.error(error)
        this.loading = false
      }); 
    },
    saveRecipe() {
      return
    },
  },
  data() {
    return {
      loading: false,
    }
  }
}
</script>

<style scoped>
.recipe, .leftTabs, .rightTabs, .table {
  height: 100%;
  background: white;
}
.leftTabs, .rightTabs {
  width: 25%;
}
.table {
  min-width: 50%;
  max-width: 50%;
}
</style>